
@media screen and (max-width: 868px) {
  .circle-container {
    overflow: auto;
    white-space: nowrap;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
}
.circle {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: inline-block;
}

  .circle span {
    font-size: 10px; /* Ajusta el tamaño del texto si es necesario */
  }
}
@media screen and (min-width: 868px) {

.circle-container {
    /* overflow: auto; */
    /* white-space: nowrap; */
    width: 100%;
    display: flex
;
    gap: 20px;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    margin-bottom: 15px;
}


/* Estilo de cada círculo */
.circle {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    /* display: inline-block; */
}

}
::-webkit-scrollbar {
    display: none;
}

.circle:hover {
  transform: scale(1.1);
}

/* Imagen dentro del círculo */
.circle img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Texto sobre la imagen */
.circle span {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 12px;
  font-weight: bold;
  white-space: nowrap;
}